import { createUseStyles } from "react-jss";
import { Fragment } from "react";
import { useSelector } from "react-redux";
import { getUploadOneViewUrl } from "../../../../utils/urls";
import showImageSkin from "../../../../skins/show-image.skin";

const useStyles = createUseStyles(showImageSkin);

function InputUploadMultipleReadOnly({ value, className, id, fieldOptions }) {
  const showImagePreview = useSelector(
    (state) => state.configuration.showImagePreview
  );

  const classes = useStyles();

  return (
    <span className={className} id={id}>
      {value ? (
        value.split(",").map((filename, index) => {
          const filenamePublicPath = getUploadOneViewUrl({
            fieldOptions,
            filename,
          });
          return (
            <Fragment key={index}>
              <a href={filenamePublicPath} target="_blank" rel="noreferrer">
                {showImagePreview &&
                /(\.jpg|\.jpeg|\.png|\.gif)$/i.test(filename) ? (
                  <img
                    src={filenamePublicPath}
                    alt={filename}
                    className={classes["image-preview"]}
                  />
                ) : (
                  filename
                )}
              </a>{" "}
            </Fragment>
          );
        })
      ) : (
        <>&nbsp;</>
      )}
    </span>
  );
}

export default InputUploadMultipleReadOnly;
